/*_____________ Owl Carousel _____________________*/

@import "standard/owl-carousel";

// .main-slider-wrapp
//   .owl-carousel
//     position: static
//     .owl-item, .owl-stage, .owl-stage-outer
//       position: static

.carousel-wrapp {
  padding: 30px 40px 20px 50px; }

.carousel-item {
  &__tt {
    font-family: $site-font-family-2, Arial, sans-serif;
    font-weight: normal;
    font-size: 16px;
    letter-spacing: -0.4px;
    color: $cl-taupe-gray; }
  &__txt {
    color: $cl-olive-haze; } }

.carousel-st_light {
  .carousel-item__tt, .carousel-item__txt {
    color: $cl-mine-shaft; } }

.owl-carousel_nav-hidden.owl-carousel {
  .owl-nav {
    display: none; } }

.owl-carousel_dots-hidden.owl-carousel {
  .owl-dots {
    display: none; } }

/* Navigation */
$owl-nav-wh: 40px;
$owl-nav-arr-w: 20;
$owl-nav-arr-h: 20;

.owl-prev, .owl-next {
  position: absolute;
  top: 50%;
  width: $owl-nav-wh;
  height: $owl-nav-wh;
  margin: 0;
  margin-top: -2px;
  padding: 0;
  opacity: 0.9; }

.owl-prev:hover, .owl-next:hover {
  opacity: 1; }

.owl-prev:before, .owl-next:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%; }

.owl-prev {
  left: -49px;
  &:before {
    @include corner-left(16px, 16px, 16px, $cl-site);
    margin-left: -8px;
    margin-top: -16px; } }


.owl-next {
  right: -37px;
  &:before {
    @include corner-right(16px, 16px, 16px, $cl-site);
    margin-left: -8px;
    margin-top: -16px; } }


//------------------------------
/* Dots */
$owl-dot-wh: 10;
$owl-dot-margin: 5;
$wh-dots: $owl-dot-wh+($owl-dot-margin*2);

.carousel-st-wrapp {
  overflow: hidden;
  padding-bottom: 40px; }

.owl-dots {
  position: absolute;
  bottom: -30px;
  width: 100%;
  text-align: center; }

.owl-dot {
  display: inline-block;
  position: relative;
  width: $owl-dot-wh+(px);
  height: $owl-dot-wh+(px);
  border-radius: 50%;
  background-color: lighten(#000, 50%);
  margin: 0 $owl-dot-margin+(px);

  // box-sizing: content-box
  // background-clip: content-box
  // border: 2px solid transparent

  &.active {
    width: $owl-dot-wh+(px);
    height: $owl-dot-wh+(px);
    background-color: #000;

    // border-color: #000
    // background-clip: padding-box
    // box-sizing: padding-box
 }    // background-color: transparent

  &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    width: $wh-dots+(px);
    height: $wh-dots+(px);
    margin-top: -($wh-dots/2)+(px);
    margin-left: -($wh-dots/2)+(px);
    border-radius: 50%;
    background-color: transparent; } }

/*____________end Owl Carousel _____________________*/
