.owl-carousel {
  position: relative;
  -webkit-tap-highlight-color: transparent;

  .owl-item {
    position: relative;
    -webkit-tap-highlight-color: transparent; }

  z-index: 1;
  display: none;
  width: 100%;

  .owl-stage {
    position: relative;
    -ms-touch-action: pan-Y;
    -moz-backface-visibility: hidden;

    &:after {
      content: ".";
      display: block;
      height: 0;
      clear: both;
      line-height: 0;
      visibility: hidden; } }

  .owl-stage-outer {
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0, 0, 0); }

  .owl-item, .owl-wrapper {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden; }

  .owl-item {
    float: left;
    min-height: 1px;
    -webkit-backface-visibility: hidden;
    -webkit-touch-callout: none; } }

.slider-clear.owl-carousel .owl-item img {
  width: auto; }

.owl-carousel {
  .owl-dots.disabled, .owl-nav.disabled {
    display: none; } }

.no-js .owl-carousel {
  display: block; }

.owl-carousel {
  &.owl-loaded {
    display: block; }

  .owl-dot {
    cursor: pointer;
    cursor: hand;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -khtml-user-select: none; }

  .owl-nav {
    .owl-next, .owl-prev {
      cursor: pointer;
      cursor: hand;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      -khtml-user-select: none; } }

  &.owl-loading {
    display: block;
    opacity: 0; }

  &.owl-hidden {
    opacity: 0; }

  &.owl-refresh .owl-item {
    visibility: hidden; }

  &.owl-drag .owl-item {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }

  &.owl-grab {
    cursor: move;
    cursor: grab; }

  &.owl-rtl {
    direction: rtl;

    .owl-item {
      float: right; } }

  .animated {
    animation-duration: 1s;
    animation-fill-mode: both; }

  .owl-animated-in {
    z-index: 0; }

  .owl-animated-out {
    z-index: 1; }

  .fadeOut {
    animation-name: fadeOut; } }

@keyframes fadeOut {
  0% {
    opacity: 1; }

  100% {
    opacity: 0; } }

.owl-height {
  transition: height .5s ease-in-out; }

.owl-carousel {
  .owl-item {
    .owl-lazy {
      transition: opacity .4s ease;
      opacity: 0; }

    img.owl-lazy {
      transform-style: preserve-3d; } }

  .owl-video-wrapper {
    position: relative;
    height: 100%;
    background: #000000; }

  .owl-video-play-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    width: 80px;
    height: 80px;
    margin-top: -40px;
    margin-left: -40px;
    background: url(owl.video.play.png) no-repeat;
    cursor: pointer;
    transition: transform .1s ease;
    -webkit-backface-visibility: hidden;

    &:hover {
      -ms-transform: scale(1.3, 1.3);
      transform: scale(1.3, 1.3); } }

  .owl-video-playing {
    .owl-video-play-icon, .owl-video-tn {
      display: none; } }

  .owl-video-tn {
    height: 100%;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    transition: opacity .4s ease;
    opacity: 0; }

  .owl-video-frame {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%; } }
